import { useFrame } from "@react-three/fiber";
import { useEffect, useRef, useState } from "react";
import { easing } from 'maath'
import getUuid from 'uuid-by-string'
import { Box, Cone, useCursor } from "@react-three/drei";
import { useRoute, useLocation } from 'wouter'
import { Euler, Material, Quaternion, Vector3 } from 'three'

const GOLDENRATIO: number = 1.61803398875;
const imgUrl = (id: string) => `./assets/project-images/${id}`
const images = [
    // example project image
    { position: [-6, 2, -4.7], rotation: [0, 0, 0], url: imgUrl('test.jpeg'), scale: new Vector3(2, GOLDENRATIO / 1, 0.05) },
    { position: [-6, 2, 4.7], rotation: [0, Math.PI, 0], url: imgUrl('test1.jpeg'), scale: new Vector3(2, GOLDENRATIO / 1, 0.05) },
    { position: [6, 2, 4.7], rotation: [0, Math.PI, 0], url: imgUrl('test2.jpeg'), scale: new Vector3(2, GOLDENRATIO / 1, 0.05) },
    { position: [6, 2, -4.7], rotation: [0, 0, 0], url: imgUrl('test3.jpeg'), scale: new Vector3(2, GOLDENRATIO / 1, 0.05) },
    //Images below are hit boxes to navigate to other rooms
    { position: [0, 1, -7], rotation: [0, 0, 0], url: imgUrl('test4.jpeg') },
    //this one is for the temportary sign in the middle of the room
    { position: [-3, 2, 0], rotation: [0, (3*Math.PI/2), 0], url: imgUrl('test5.jpeg') }
]

export default function PhotoFrames({ rotation = new Quaternion().setFromEuler(new Euler(0, (3 * Math.PI) / 2, 0)), position = new Vector3() }: { rotation?: any, position?: any }) {

    const ref: any = useRef()
    const clicked: any = useRef()
    const [, params] = useRoute('/item/:id')
    const [, setLocation] = useLocation()

    useEffect(() => {
        clicked.current = ref.current.getObjectByName(params?.id)
        if (clicked.current) {
            clicked.current.parent.updateWorldMatrix(true, true)
            clicked.current.parent.localToWorld(position.set(0, 0, 1.25))
            clicked.current.parent.getWorldQuaternion(rotation)
        } else {
            position.set(-10, 2, 0.02)
        }
    })

    useFrame((state, dt) => {
        easing.damp3(state.camera.position, position, 0.4, dt)
        easing.dampQ(state.camera.quaternion, rotation, 0.4, dt)
    })

    return (
        <group
            ref={ref}
            onClick={(e) => (e.stopPropagation(), setLocation(clicked.current === e.object ? '/' : '/item/' + e.object.name))}
            onPointerMissed={() => setLocation('/')}>
            {images.map((props: any) => <Frame key={props.url} {...props} scale={props.scale} />)}
        </group>
    );
}

function Frame({ url, ...props }: { url: string;[key: string]: any }) {
    const [hovered, hover] = useState(false)
    const name = getUuid(url)
    useCursor(hovered)

    return (
        <group position={props.position} rotation={props.rotation}>
            <Box
                name={name}
                // material-color="#FF0000"
                onPointerOver={(e) => (e.stopPropagation(), hover(true))}
                onPointerOut={() => hover(false)}
                scale={props.scale}
            // position={[0, GOLDENRATIO / 1.5, 0]}
            >
                <meshBasicMaterial transparent={true} opacity={0} />
            </Box>
        </group>
    )
}